@import "../../style/index";

.promoteContentArea {
  text-align: center;
  padding-top: 275px;
  padding-bottom: 275px;
  font-size: 0px;
  div {
    p {
      font-size: 40px;
      line-height: 1.3;
    }

  }
}

.darkTitle {
  text-align: center;
  p {
    color: $text-second;
  }
}

.lightTitle {
  text-align: center;
  color: #fff;
  p {
    color: #fff;
  }
}

.getIdContainer {
  height: 42px;
  border-radius: 21px;
  border: 1px solid rgba(0, 120, 255, 1);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 8px 38px;
  margin-top: 10px;
  cursor: pointer;
  img {
    width: 30px;
    height: 30px;
  }
  p {
    font-size: 16px;
    margin-bottom: 0;
    font-weight: bold;
    color: #0078FF;
    margin-left: 20px;
    text-decoration: none !important;
  }
}

a.iteringidLink:hover{
  text-decoration: none !important;
}
a.dkmsDemoLink{
  color: #080C20;
  font-weight: bold;
  cursor: pointer;
}
.LandscapeBox {
  position: relative;
  .mapBox {
    display: flex;

    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    & > div {
      flex: 1;
      height: 30vh;
      align-self: flex-end;
      cursor: pointer;
      & > div {
        width: 100%;
        padding: 20px;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 4px 6px 0px rgba(0, 0, 0, 0.1);
        font-size: 15px;
        color: $text-second;
        text-align: left;
        margin-top: -17vh;
      }
    }

    .area1 {
      margin-bottom: 20vh;
    }
    .area2 {
      margin-bottom: 7vh;
    }
    .area3 {
      margin-bottom: 20vh;
      //&>div{
      //  margin-top: -10vh;
      //}
    }
  }
}

.Landscape {
  width: 100%;
  padding: 1rem;
}

.homeBannerArea {
  background: url("./img/home-banner.png") top center no-repeat;
  //background-size: auto 100%;
  background-size: cover;
}

.walletModuleBannerArea {
  background: url("./img/wallet-module-banner.jpg") top center no-repeat;
  //background-size: auto 100%;
  background-size: cover;
  .featureBox {
    & > div > div {
      padding: 20px;
      background: #000000D0;
      border-radius: 5px;
      margin-top: 10px;
      margin-bottom: 10px;
      min-height: 300px;

      flex-direction: column;
      display: flex;
      align-items: center;

      img {
        width: 100px;
      }

      p {
        font-size: 15px;
        flex: 1;
      }

      .link {
        color: #4A90E2;
      }
    }
  }
}

.line {
  display: inline-block;
  height: 10px;
  width: 258px;
  background-color: #104dcd;
  margin: 8px;
}

.spaceSection {
  padding-top: 110px;
  padding-bottom: 110px;
  .sectionTitle {
    h1 {
      font-size: 38px;
      font-weight: bold;
    }
    p {
      font-size: 15px;
    }
  }
}

.lgSpaceSection {
  @extend .spaceSection;
  padding-top: 230px;
  padding-bottom: 230px;
  .sectionTitle {
    margin-bottom: 70px;
    p {
      font-weight: bold;
    }
  }
}

.solutionBox {
  .solutionImage {
    width: 100%;
  }
  .solutionDescription {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  text-align: left;
  margin-top: 100px;
  h1 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  p {
    font-size: 15px;
  }

}

@media (max-width: 575.98px) {
  .promoteContentArea {
    padding-top: 105px;
    padding-bottom: 105px;
    div {
      p {
        font-size: 22px;
        line-height: 1.3;
      }
    }
  }

  .line {
    height: 5px;
    width: 78px;
  }

  .spaceSection {
    padding-top: 50px;
    padding-bottom: 50px;
    .sectionTitle {
      h1 {
        font-size: 22px;
      }
    }
  }

  .lgSpaceSection {

    .sectionTitle {
      margin-bottom: 30px;

    }
  }

  .solutionBox {
    margin-top: 0px;
    h1, p {
      text-align: center;
    }

    h1 {
      font-size: 18px;
    }

    .solutionDescription {
      margin-bottom: 50px;
    }
  }
}
